<template>
	
	<ul class="pagination" v-if="pagesCount > 1">
		<li class="pag-control" :class="{ disabled: currentPage === 1 }">
			<router-link :to="{path: currentPath}" exact title="First page">
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" svg-inline="" role="presentation" focusable="false" tabindex="-1"><path opacity=".87" fill="none" d="M24 0v24H0V0h24z"/><path class="base" d="M18.41 16.59L13.82 12l4.59-4.59L17 6l-6 6 6 6 1.41-1.41zM6 6h2v12H6V6z"/></svg>
			</router-link>
		</li>

		<li class="pag-control" :class="{ disabled: currentPage === 1 }">
			<router-link :to="{path: currentPath, query: {page: currentPage - 1}}" exact title="Previous">
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" svg-inline="" role="presentation" focusable="false" tabindex="-1"><path fill="none" d="M0 0h24v24H0V0z"/><path class="base" d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12l4.58-4.59z"/></svg>
			</router-link>
		</li>


		<li v-for="page in pagesShown" :key="page"
			:class="{ active: (routerCurrentPage === page) || (!routerCurrentPage && (page === 1)) }">
			<router-link v-if="page === 1" :to="{path: currentPath}" exact>
				{{ page }}
			</router-link>
			<router-link v-else :to="{path: currentPath, query: {page: page}}" exact>
				{{ page }}
			</router-link>
		</li>


		<li class="pag-control" :class="{ disabled: currentPage === pagesCount }">
			<router-link :to="{path: currentPath, query: {page: currentPage + 1}}" exact title="Next">
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" svg-inline="" role="presentation" focusable="false" tabindex="-1"><path fill="none" d="M0 0h24v24H0V0z"/><path class="base" d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z"/></svg>
			</router-link>
		</li>

		<li class="pag-control" :class="{ disabled: currentPage === pagesCount }">
			<router-link 
				:to="{path: currentPath, query: {page: pagesCount}}" 
				exact :title="'Page ' + pagesCount">
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" svg-inline="" role="presentation" focusable="false" tabindex="-1"><path opacity=".87" fill="none" d="M0 0h24v24H0V0z"/><path class="base" d="M5.59 7.41L10.18 12l-4.59 4.59L7 18l6-6-6-6-1.41 1.41zM16 6h2v12h-2V6z"/></svg>
			</router-link>
		</li>
	</ul>
</template>

<script>
export default {
	props: ['postsLength'],
	data() {
		return {
			pagesShownCount: 5
		}
	},

	computed: {
		pagesCount() {
			return Math.ceil(this.postsLength / this.$store.state.postsPerPage)
		},
		currentPage() {
			const page = this.routerCurrentPage
			return page ? page : 1
		},
		routerCurrentPage() {
			return this.$route.query.page
		},
		currentPath() {
			return this.$route.path
		},
		pagesShown() {
			if (this.pagesShownCount >= this.pagesCount) return this.pagesCount

			const sideCount = (this.pagesShownCount % 2 === 0)
								? this.pagesShownCount / 2
								: (this.pagesShownCount - 1) / 2

			let pages = []
			for (let i = 1; i <= this.pagesShownCount; i++) {
				pages.push(i)
			}

			if (this.currentPage > sideCount + 1) {
				pages.forEach((pagenum, i, arr) => {
					arr[i] = this.currentPage - sideCount - 1 + +pagenum
				})
			}
			if (this.currentPage > this.pagesCount - sideCount) {
				pages.forEach((pagenum, i, arr) => {
					arr[i] = this.pagesCount - this.pagesShownCount + i + 1
				})
			}

			return pages
		}
	}

}
</script>

<style lang="scss" scoped>
.pagination {
	margin-top: 4rem;
	text-align: center;
}
.pag-control {
	display: inline-flex;

	a {
		display: flex;
		align-items: center;
	}
	&.disabled {
		opacity: .5
	}
	&:not(.disabled) {
		&:hover, &:focus {
			.base {
				fill: #009688
			}
		}
	}
}
.pagination li.active {
	background: #009688;
}
</style>